import React from 'react';
import { useStaticQuery, graphql } from 'gatsby';

// components
import Layout from '../components/layout';
import SEO from '../components/seo';
import About from '../components/about';
import WorkExperience from '../components/work-experience';
import Education from '../components/education';
import Skills from '../components/skills';
import Awards from '../components/awards';
import Intro from '../components/intro';

const IndexPage = () => {
  // TODO: this query is nuts...
  const { dataJson: copy } = useStaticQuery(
    graphql`
      query MyQuery {
        dataJson {
          intro {
            title
            metaData {
              logoLeft
              logoRight
            }
            background {
              childImageSharp {
                fluid {
                  ...GatsbyImageSharpFluid
                }
              }
            }
          }

          about {
            body
          }

          workExperience {
            icon
            title
            jobs {
              companyName
              duties
              endYear
              startYear
              title
            }
          }

          education {
            icon
            schools {
              courses
              degree
              endYear
              program
              schoolName
              startYear
              twoColumn
            }
            title
          }

          skills {
            heading {
              icon
              title
            }
            section1 {
              column1 {
                list
                title
              }
              column2 {
                list
                title
              }
              column3 {
                list
                title
              }
              column4 {
                list
                title
              }
              heading {
                icon
                title
              }
            }
            section2 {
              column1 {
                list
                title
              }
              column2 {
                list
                title
              }
              heading {
                icon
                title
              }
            }
          }

          awards {
            heading {
              icon
              title
            }
            list {
              description
              year
            }
          }
        }
      }
    `
  );

  return (
    <Layout pageInfo={{ pageName: 'index' }}>
      <SEO
        title="Jamie Kelly - Software Engineer (Web)"
        keywords={[`gatsby`, `react`, `bootstrap`]}
      />
      <Intro {...copy.intro} />
      <About {...copy.about} />
      <WorkExperience {...copy.workExperience} />
      <Education {...copy.education} />
      <Skills {...copy.skills} />
      <Awards {...copy.awards} />
    </Layout>
  );
};

export default IndexPage;
