import React from 'react';
import { Container, Row, Col } from 'react-bootstrap';
import Heading from './heading';
import SubHeading from './sub-heading';

const renderSkill = (item, index) => (
  <li dangerouslySetInnerHTML={{ __html: item }} key={`award-${index}`} />
);

// TODO: this could benefit from some componentization
// perhaps create a section component since all columns are the same size
// and if a column edge exists then render the column
// also create a list component with title
const Skills = ({ heading, section1, section2 }) => (
  <section id="skills-techniques">
    <Container>
      <Heading icon={heading.icon} title={heading.title} />

      <SubHeading icon={section1.heading.icon} title={section1.heading.title} />
      <Row data-aos="fade-up">
        <Col sm={7} lg={{ span: 6, offset: 1 }}>
          <strong>{section1.column1.title}</strong>
          <ul>{section1.column1.list.map(renderSkill)}</ul>
        </Col>
        <Col sm={5} lg={5}>
          <strong>{section1.column2.title}</strong>
          <ul>{section1.column2.list.map(renderSkill)}</ul>
        </Col>
      </Row>
      <Row data-aos="fade-up">
        <Col sm={7} lg={{ span: 6, offset: 1 }}>
          <strong>{section1.column3.title}</strong>
          <ul className="two-column">
            {section1.column3.list.map(renderSkill)}
          </ul>
        </Col>
        <Col sm={5} lg={5}>
          <strong>{section1.column4.title}</strong>
          <ul className="two-column">
            {section1.column4.list.map(renderSkill)}
          </ul>
        </Col>
      </Row>

      <SubHeading icon={section2.heading.icon} title={section2.heading.title} />
      <Row data-aos="fade-up">
        <Col sm={7} lg={{ span: 6, offset: 1 }}>
          <strong>{section2.column1.title}</strong>
          <ul className="two-column">
            {section2.column1.list.map(renderSkill)}
          </ul>
        </Col>
        <Col sm={5} lg={5}>
          <strong>{section2.column2.title}</strong>
          <ul>{section2.column2.list.map(renderSkill)}</ul>
        </Col>
      </Row>
    </Container>
  </section>
);

export default Skills;
