import React from 'react';
import { Row, Col } from 'react-bootstrap';

const renderSingleColumn = (course, index) => (
  <dd key={`course-${index}`} className="list-item">
    {course}
  </dd>
);

const renderTwoColumn = (course, index) => (
  <li key={`course-${index}`}>{course}</li>
);

const School = ({
  startYear,
  endYear,
  schoolName,
  program,
  degree,
  courses,
  twoColumn,
}) => (
  <Row className="spacer" data-aos="fade-up">
    <Col
      sm={{ span: 3, offset: 1 }}
      lg={{ span: 3, offset: 1 }}
      className="no-padding-left"
    >
      <p>
        {startYear} - {endYear}
      </p>
    </Col>
    <Col sm={8} lg={8} xs={12}>
      <dl>
        <dt>
          <strong>{schoolName} | </strong>
          <em>{program}</em>
        </dt>
        {degree && <dt className="subtitle">{degree}</dt>}
        {twoColumn ? (
          <dd>
            <ul className="two-column">{courses.map(renderTwoColumn)}</ul>
          </dd>
        ) : (
          courses.map(renderSingleColumn)
        )}
      </dl>
    </Col>
  </Row>
);

export default School;
