import React from 'react';
import { Container, Row, Col } from 'react-bootstrap';
import Heading from './heading';

const renderAward = ({ year, description }, index) => (
  <Row className="row" data-aos="fade-up" key={`award-${index}`}>
    <Col
      sm={{ span: 2, offset: 1 }}
      lg={{ span: 1, offset: 1 }}
      className="no-padding-left"
    >
      <p>{year}</p>
    </Col>
    <Col sm={9} lg={10}>
      <strong>{description}</strong>
    </Col>
  </Row>
);

const Awards = ({ heading, list }) => (
  <section id="awards-recognition">
    <Container>
      <Heading icon={heading.icon} title={heading.title} />
      {list.map(renderAward)}
    </Container>
  </section>
);

export default Awards;
